function ListItem({ id, title, status, handleDelete }) {
  const style = { display: "flex", gap: 8, justifyContent: "center" };

  const newStatus = status ? "Done" : "Not Done";
  return (
    <div key={id} data-testid="list-container" style={style}>
      <p data-testid="list-item">
        <b data-testid="list-item-title">{title}</b>
        <span data-testid="list-item-status">{newStatus}</span>
      </p>
      <button onClick={() => handleDelete(id)} data-testid="delete-btn">
        DELETE
      </button>
    </div>
  );
}

export default ListItem;
