import { useState } from "react";

function AddItem({ max, limit, handleAdd }) {
  const [text, setText] = useState("");

  return (
    <div>
      <input
        value={text}
        onChange={(e) => setText(e.target.value)}
        data-testid="input"
        placeholder="add something"
      />
      <button
        disabled={limit === max}
        onClick={() => handleAdd(text)}
        data-testid="add-btn"
      >
        ADD
      </button>
    </div>
  );
}

export default AddItem;
